import React from 'react';
import GameStateManager from '../GameStateManager.js';

class HowToPlayButton extends React.Component {

    onClick() {
        GameStateManager.showHowToPlay();
    }

    render() {
        return (
            <a href='#' onClick={this.onClick.bind(this)}>How to play</a>
        )
    }

}

export default HowToPlayButton;


