import React from 'react';
import './App.css';
import Gameboard from './components/Gameboard.js';
import Gameinfo from './components/Gameinfo.js';
import Header from './components/Header.js';
import PregameWindow from './components/pregame_sections/PregameWindow.js';
import HowToPlayWindow from './components/HowToPlayWindow.js';
import GameStateManager from './GameStateManager.js';
import GameStateCommandStore from './stores/GameStateCommandStore.js';

class App extends React.Component {
	constructor(){
		super();

		this.state = {
			pregame_info_is_active: true,
			how_to_play_is_active: false,

		};
		return;
	}

	componentWillMount() {
		GameStateCommandStore.on("SHOW_HOW_TO_PLAY", () => {
			this.setState({
				how_to_play_is_active: true,
			});
		});
		return;
	}

	onPregameInfoDone() {
		this.setState({
			pregame_info_is_active: false
		});
		GameStateManager.start();
		return;
	}

	onHowToPlayDone() {
		this.setState({
			how_to_play_is_active: false
		});
		return;
	}

	render() {
		return (
			<div>
				{this.state.pregame_info_is_active && <PregameWindow showHowToPlay={this.state.how_to_play_active} onDone={this.onPregameInfoDone.bind(this)} />}
				{this.state.how_to_play_is_active && <HowToPlayWindow onDone={this.onHowToPlayDone.bind(this)} />}
				<div className={(this.state.pregame_info_is_active || this.state.how_to_play_is_active) ? 'screen-blur' : ''}>
					<Header />
					<Gameboard />
					<Gameinfo />
				</div>

			</div>
		);
	}


}

export default App;



