import React from 'react';
import player_route_img from '../img/player_route.PNG';
import roll_dice_img from '../img/roll_dice_button.svg';
import rosette_img from '../img/flower.svg';

class HowToPlayWindow extends React.Component {

    onDoneClick() {
        this.props.onDone();
        return;
    }

    render() {
        return (
            <div className='pregame-window in'>
                <center>
                    <h1>How to play</h1>
                </center>
                <div className='how-to-play-container'>
                    <div className='how-to-play-description'>
                        <h3>The Royal Game of Ur </h3>
                    </div>
                    <div className='how-to-play-description'>
                        <p>is a race game. The objective is to move all of your markers to the finish line.</p>
                    </div>
                    <h2>The basics.</h2>
                        <h3>Rolling the dice.</h3>
                            <img src={roll_dice_img} alt='roll dice button' />
                            <p>First thing to do on your turn is to roll the dice. You can roll the dice by clicking the dice button below.</p>
                            <p>Dice with a black circle on top earn you 1 tile to move. You can roll 0 to 4.</p>
                        <h3>Moving the markers.</h3>
                            <p>After rolling the dice, you can move a marker the amount you rolled. Do this by clicking on a marker, then clicking on the tile you want to move to.</p>
                            <p>You can only move your marker on your route. The route for each player is shown below.</p>
                            <center><img className='how-to-play-player-route-img' src={player_route_img} alt="Player route info" /></center>
                        <h3>Attacking</h3>
                            <p>Moving your markers onto the other player's markers will remove them from the board.</p>
                        <h3>Rosettes</h3>
                            <img className='how-to-play-flower-img' src={rosette_img} alt='rosette' />
                            <p>Landing on a rosette grants you one extra roll and move. The rosette in the center of the board also grants immunity to other player's attacks.</p>
                </div>
                <div>
                    <button className='pregame-next-button' onClick={this.onDoneClick.bind(this)}>I'm an expert now!</button>
                </div>
            </div>
        )
    }
}

export default HowToPlayWindow;


